<template>
    <div class="people-position-page">
        <div class="position-item">
            <div class="title">户籍地</div>
            <div v-if="!showHujiForm" class="show-hujidi">
                <el-button type="primary" size="small" @click="handleShowForm('hujidi')">是否修改户籍地位置</el-button>
                <div class="address-item">
                    <div class="title">户籍地详址：</div>
                    <div class="address">{{ hujidi }}</div>
                </div>
            </div>
            <vPositionItem v-else ref="houseItem" :positionRef="'houseItem'" :xingzhengqu="xingzhengqu" :disCodeOption="disCodeOption" :isClick="false"></vPositionItem>
        </div>
        <div class="position-item">
            <div class="title">现住地</div>
            <div v-if="!showXianzhuForm" class="show-xianzhudi">
                <el-button type="primary" size="small" @click="handleShowForm('xianzhudi')">是否修改现住地位置</el-button>
                <div class="address-item">
                    <div class="title">现住地详址：</div>
                    <div class="address">{{ xianzhudi }}</div>
                </div>
            </div>
            <vPositionItem v-else ref="liveHouseItem" :positionRef="'liveHouseItem'" :xingzhengqu="xingzhengqu" :disCodeOption="disCodeOption" :isClick="true"></vPositionItem>
        </div>
    </div>
</template>

<script>
import vPositionItem from "./PositionItem.vue";
export default {
  components: {
    vPositionItem
  },
  props: {
    isAdd: {
      type: Boolean,
      default: true
    },
    xingzhengqu: {
      type: Array,
      default: []
    },
    disCodeOption: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      hujidi: "",
      xianzhudi: "",
      showHujiForm: true,
      showXianzhuForm: true
    };
  },

  methods: {
    destroyedForm() {
      this.$refs["houseItem"] && this.$refs["houseItem"].resetForm();
      this.$refs["liveHouseItem"] && this.$refs["liveHouseItem"].resetForm();
      this.showHujiForm = true;
      this.showXianzhuForm = true;
    },
    handleShowForm(type) {
      if (type == "hujidi") {
        this.hujidi = "";
        this.showHujiForm = true;
        this.$nextTick(() => {
          this.$refs["houseItem"].initGridData();
        });
      } else {
        this.xianzhudi = "";
        this.showXianzhuForm = true;
        this.$nextTick(() => {
          this.$refs["liveHouseItem"].initGridData();
        });
      }
    }
  }
};
</script>

<style lang="scss">
.people-position-page {
  display: flex;
  .position-item {
    margin: 0 10px;
    width: calc(50% - 20px);
    border: 1px solid #c7c7c7;
    padding: 10px;
    .title {
      font-size: 18px;
      font-weight: 600;
      text-align: center;
    }
    .address-item {
      font-size: 18px;
      padding: 10px 0;
      color: #000;
      display: flex;
      align-items: center;
      .title {
        width: 125px;
      }
      .address {
        font-size: 24px;
        color: #ff310f;
      }
    }
  }
}
</style>
