<template>
    <el-form class="lable-item" :ref="positionRef" :model="positionForm" :rules="positRules" label-width="50px">
        <el-row :gutter="20">
            <el-col :span="8">
                <el-form-item label="省" label-width="90px">
                    <el-select placeholder="请选择省" :disabled="!isClick" v-model="positionForm.province">
                        <el-option v-for="(item, index) in xingzhengqu" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>  
            </el-col>
            <el-col :span="8">
                <el-form-item label="市" v-if="isShowCity == true">
                    <el-select placeholder="请选择市" :disabled="!isClick" v-model="positionForm.city">
                        <el-option v-for="(item, index) in cityOptions" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="8">
                <el-form-item label="区县" v-if="isShowCounty == true" prop="county">
                    <el-select placeholder="请选择区县" v-model="positionForm.county" @change="handleCounty">
                        <el-option v-for="(item, index) in countyOptions" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row :gutter="20" v-if="isShowCounty == true">
            <el-col :span="16">
            <el-form-item label="街道社区" prop="grid" label-width="90px">
                <el-cascader placeholder="请选择街道社区网格" :options="disOption" filterable clearable change-on-select 
                    v-model="positionForm.grid" :props="{value: 'code' }"></el-cascader>
            </el-form-item>  
            </el-col>
            <el-col :span="8">
            <el-form-item label="楼栋" prop="buildId">
                <el-select placeholder="请选择楼栋" v-model="positionForm.buildId" clearable @change="handleBuildByHouse">
                    <el-option v-for="(item, index) in buildOptions" :label="item.name" :value="item.id" :key="index"></el-option>
                </el-select>
            </el-form-item>  
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
            <el-form-item label="户籍地详址" label-width="90px">
                <el-input v-model="positionForm.domicilePlaceDetail" :disabled="!isClick || isShowCounty" placeholder="请填写户籍地祥址"></el-input>
            </el-form-item>
            </el-col>
        </el-row>
        <div class="house-list" v-if="isShowCounty == true">
            <el-tabs v-model="queryForm.unitCount" class="house-unit">
                <el-tab-pane class="house-unit-pane" v-for="(item, index) in buildInfo.unitCount" :label="NumToString(index+1, 'unit')" :name="String(index+1)" :key="index">
                    <el-tabs tab-position="left" v-model="queryForm.floorCount" class="house-floor">
                        <el-tab-pane v-for="(item, index) in buildInfo.floorCount" :label="NumToString(index+1, 'floor')" :name="String(index+1)" :key="index">
                            <el-radio-group v-model="positionForm.houseId">
                                <el-radio class="house-item" :label="item.id" v-for="(item, index) in houseData" :key="index">
                                    <div class="house-name">{{ item.houseNum }}</div>
                                    <div class="house-img">
                                        <img class="house_img" :src="houseImg" />
                                    </div>
                                    <div class="house-num">{{ item.peopleCount }} 人</div>
                                </el-radio>
                            </el-radio-group>
                        </el-tab-pane>
                    </el-tabs>
                </el-tab-pane>
            </el-tabs>
        </div>
    </el-form>
</template>

<script>
import config from "./config/select-config.js";
import service from "../services/infact_people_service.js";

export default {
  props: {
    xingzhengqu: {
      type: Array,
      default: []
    },
    disCodeOption: {
      type: Array,
      default: []
    },
    isClick: {
      type: Boolean,
      default: false
    },
    positionRef: {
        type: String,
        default: "houseItem"
    }
  },
  data() {
    return {
      cityOptions: config.cityOptions,
      countyOptions: config.countyOptions,
      disOption: [], // 行政区数据
      buildOptions: [], // 楼栋数据
      houseData: [], // 户数据
      houseImg: require("./images/house.png"),
      positionForm: {
        province: "610000",
        city: "610200",
        county: "",
        grid: [],
        buildId: "", // 楼栋ID
        domicilePlaceDetail: "",
        houseId: "" // 户籍地户ID
      },
      gridName: "",
      buildInfo: {
        unitCount: "",
        floorCount: ""
      }, // 选中楼栋信息
      houseInfo: {}, // 户信息
      queryForm: {
        unitCount: "1",
        floorCount: "1"
      },
      positRules: {
        county: [{ required: true, message: "请选择区县", trigger: "change" }],
        grid: [
          { required: true, message: "请选择街道社区网格", trigger: "change" }
        ],
        buildId: [{ required: true, message: "请选择楼栋", trigger: "change" }]
      },
      isShowCity: true, // 是否显示市
      isShowCounty: true // 是否显示其他输入框
    };
  },

  mounted() {
    disOption: {
      return this.disCodeOption[0].children;
    }
  },

  watch: {
    /** 监听省选择 */
    "positionForm.province"(value) {
      if (value != "610000") {
        this.isShowCity = false;
        this.isShowCounty = false;
      } else {
        this.positionForm.city = "610200";
        this.isShowCity = true;
        this.isShowCounty = true;
      }
      let temp = _.find(this.xingzhengqu, item => {
        return value == item.code;
      });
      this.positionForm.domicilePlaceDetail = temp && temp.name;
    },
    /** 监听市选择 */
    "positionForm.city"(value) {
      let tempCity = _.find(this.cityOptions, item => {
        return value == item.code;
      });
      if (value == "610200") {
        this.isShowCounty = true;
        this.positionForm.county = "";
        this.disOption = [];
        this.positionForm.buildId = "";
        this.positionForm.domicilePlaceDetail = "";
        this.buildOptions = [];
        this.houseData = [];
      } else {
        this.isShowCounty = false;
        this.positionForm.domicilePlaceDetail =
          "陕西省" + (tempCity && tempCity.name) || "";
      }
    },

    "positionForm.county"(value) {
      if (value != undefined && value != "") {
        let temp = _.find(this.countyOptions, item => {
          return value == item.code;
        });
        service.requestGridCode(data => {
          switch (data.code.length) {
            case 4:
              this.disOption = _.find(data.children, item => {
                return item.code == value;
              }).children;
              break;
            case 6:
              this.disOption = data.children;
              break;
            default:
              this.disOption = [data];
              break;
          }
        });
      }
    },
    "positionForm.grid"(value) {
      if (value.length != 0) {
        this.buildOptions = [];
        this.positionForm.buildId = "";
        this.positionForm.domicilePlaceDetail = "";
        this.houseData = [];
        this.buildInfo = {};
      }
      // 查询楼栋列表
      service.requestCodeByBuild(_.last(value), data => {
        this.buildOptions = data.list;
      });
    },

    queryForm: {
      handler(value) {
        this.positionForm.domicilePlaceDetail = "";
        if (this.buildInfo.id != undefined) {
          this.handleQueryHouse();
        }
      },
      deep: true
    },

    "positionForm.houseId"(value) {
      this.houseInfo = _.find(this.houseData, item => {
        return value == item.id;
      });
      if (value != "" && value != undefined) {
        service.requestCodeByAddress(_.last(this.positionForm.grid), data => {
          this.positionForm.domicilePlaceDetail =
            data.belongname +
            data.name +
            this.buildInfo.name +
            this.houseInfo.unit +
            "单元" +
            this.houseInfo.floor +
            "层" +
            this.houseInfo.houseNum;
        });
      }
    }
  },

  methods: {
    /** 转文字 */
    NumToString(value, type) {
      if (type == "unit") {
        return value + "单元";
      } else {
        return value + "层";
      }
    },
    /** 选中区县 */
    handleCounty() {
      this.buildInfo = {};
      this.positionForm.houseId = "";
      this.positionForm.grid = [];
      this.positionForm.buildId = ""; // 楼栋ID
      this.positionForm.domicilePlaceDetail = "";
    },
    /** 选中楼栋 */
    handleBuildByHouse(buildId) {
      this.buildInfo = _.find(this.buildOptions, item => {
        return item.id == buildId;
      });
      this.positionForm.houseId = "";
      this.queryForm = {
        unitCount: "1",
        floorCount: "1"
      };
      this.handleQueryHouse();
    },
    /** 查询户信息 */
    handleQueryHouse() {
      let param = _.cloneDeep(this.queryForm);
      param.buildingId = this.buildInfo.id;
      service.requestBuildByHouse(param, data => {
        _.map(data.list, item => {
          item.dicMap = {
            type: item.type == 1 ? "楼房" : "平房"
          };
        });
        this.positionForm.houseId =
          data.list.length != 0 ? data.list[0].id : "";
        this.houseData = data.list;
      });
    },
    /** 初始化楼栋信息 */
    initGridData() {
      let code = this.disCodeOption[0].code;
      switch (code.length) {
        case 6: // 区县
          this.positionForm.county = code;
          break;
        case 9: // 街道
          this.positionForm.county = code.slice(0, 6);
          this.positionForm.grid = [code.slice(0, 6), code];
          break;
        case 12: // 社区
          this.positionForm.county = code.slice(0, 6);
          this.positionForm.grid = [code.slice(0, 6), code.slice(0, 9), code];
          break;
        case 14: // 网格
          this.positionForm.county = code.slice(0, 6);
          this.positionForm.grid = [
            code.slice(0, 6),
            code.slice(0, 9),
            code.slice(0, 12),
            code
          ];
          break;
        default:
          break;
      }
    },

    resetForm() {
      this.$refs[this.positionRef].clearValidate();
      this.buildInfo = {
        unitCount: "",
        floorCount: ""
      };
      this.houseInfo = {};
      this.positionForm = {
        province: "610000",
        city: "610200",
        grid: [],
        buildId: "", // 楼栋ID
        domicilePlaceDetail: "",
        houseId: "" // 户籍地户ID
      };
    }
  }
};
</script>

<style lang="scss">
.lable-item {
  padding-top: 15px;
  .house-list {
    height: 450px;
    .house-unit {
      height: 100%;
      .el-tabs__header.is-top {
        margin-left: 90px;
      }
      .el-tabs__content {
        height: calc(100% - 55px);
        .house-unit-pane {
          height: 100%;
          .house-floor {
            height: 100%;
            .el-tabs__header.is-left {
              width: 70px;
            }
            .el-tabs__content {
              height: 100%;
              .el-tab-pane {
                height: 100%;
                display: flex;
                .house-item {
                  text-align: center;
                  padding: 5px;
                  width: 100px;
                  height: 100px;
                  border: 1px solid #c8e2e6;
                  margin: 10px;
                  border-radius: 5px;
                  .el-radio__input {
                    position: absolute;
                    right: 10px;
                  }
                  .house-img {
                    position: relative;
                    .house_img {
                      width: 50px;
                      height: 50px;
                    }
                  }
                  &:hover {
                    background-color: #cceaff;
                    border-radius: 5px;
                    cursor: pointer;
                    transition: all 300ms;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>

